<script>
	import Multiselect from "vue-multiselect";
	import {
		FormWizard,
		TabContent
	} from "vue-form-wizard";
	import axios from 'axios';
	import Layout from "../../layouts/main";
	import PageHeader from "@/components/page-header";
	/**
	 * written by 张文彧
	 */
	export default {
		components: {
			Layout,
			PageHeader,
			FormWizard,
			TabContent,
			Multiselect
		},
		data() {
			return {
				form: {
					departmentName: '',
					description: '',
					leaderId: ''
				},
				isAllFill: false,
				leaderName: '',
				leaderClass: '',
				title: "添加部门",
				items: [{
						text: "超级管理员"
					},
					{
						text: "添加部门",
						active: true
					}
				],
				value: null,
				value1: null,
				options: [
					"Touchscreen",
					"Call Function",
					"Notifications",
					"Fitness",
					"Outdoor"
				],
				dropzoneOptions: {
					url: "https://httpbin.org/post",
					thumbnailWidth: 150,
					maxFilesize: 0.5,
					headers: {
						"My-Awesome-Header": "header value"
					}
				}
			};
		},
		/**
		 * 判断表单是否填写完整
		 * departmentName: '',
					description: '',
					leaderId: ''
		 */
		watch: {
			/**
			 * 监听FORM
			 */
			form: {
			    handler() {
				
				  	this.isAllFill = this.form.departmentName!='' && this.form.description!='' && this.form.leaderId!=''
				  
			    },
			    deep: true
			}
		},
		methods: {
			/**
			 * 提交表单前验证表单是否填完
			 */
			beforeTabSwitch: function() {
				
				if (this.isAllFill) {
					return true;
				} else {
					this.$message({
						type: 'warning',
						message: '请填写所有必填项'
					});
					return false
				}

			},
			onComplete: function() {
				// alert('Yay. Done!');
				// /departments/insert
				axios
					.post('http://localhost:8848/departments/insert', this.form)
					.then(response => (
						// this.userList = response.data.list
						console.log(response),
						this.$message({
							type: 'success',
							message: '添加成功!'
						}),
						this.getAll(),
						this.$router.go(0)
						// this.$router.push({path:'/addDepart'})
					))
					.catch(function(error) { // 请求失败处理
						console.log(error);
					});
			},
		}
	};
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" />
		<div class="row">
			<div class="col-lg-12">
				<div class="card">
					<div class="card-body">
						<form-wizard color="#5664d2" @on-complete="onComplete">
							<tab-content title="部门信息" :before-change="beforeTabSwitch">
								<div class="tab-pane" id="basic-info">
									<h4 class="card-title">部门基本信息</h4>
									<p class="card-title-desc">请填入以下所有基本信息</p>
									<form>
										<div class="form-group">
											<label for="productname">部门名称</label>*
											<input v-model="form.departmentName" id="productname" name="productname"
												type="text" class="form-control" />
										</div>
										<div class="form-group">
											<label for="productname">Leader ID</label>*
											<input v-model="form.leaderId" id="productname" name="productname"
												type="text" class="form-control" />
										</div>
										<div class="form-group">
											<label for="productname">Leader 名称</label>
											<input v-model="leaderName" id="productname" name="productname" type="text"
												class="form-control" />
										</div>
										<div class="row">
											<div class="col-md-6">
												<div class="form-group">
													<label class="control-label">部门分类</label>
													<multiselect v-model="leaderClass" :options="['0', '1', '2']">
													</multiselect>
												</div>
											</div>
										</div>

										<div class="form-group">
											<label for="productdesc">部门描述</label>*
											<textarea v-model="form.description" class="form-control" id="productdesc"
												rows="5"></textarea>
										</div>
									</form>
								</div>
							</tab-content>
							<tab-content title="确认信息">
								<div class="tab-pane" id="product-img" style="color:#9494b8;">
									<h4 class="card-title">确认信息</h4>
									<p class="card-title-desc">请确认您的部门信息</p>

									<form>
										<div class="form-group">
											<label for="productname">部门名称</label>
											<input v-model="form.departmentName" id="productname" name="productname"
												type="text" class="form-control" readonly />
										</div>
										<div class="form-group">
											<label for="productname">Leader ID</label>
											<input v-model="form.leaderId" id="productname" name="productname"
												type="text" class="form-control" readonly />
										</div>
										<div class="form-group">
											<label for="productname">Leader 名称</label>
											<input v-model="leaderName" id="productname" name="productname" type="text"
												class="form-control" readonly />
										</div>
										<div class="row">
											<div class="col-md-6">
												<div class="form-group">
													<label for="productname">部门分类</label>
													<input v-model="leaderClass" id="productname" name="productname"
														type="text" class="form-control" />
												</div>
											</div>
										</div>

										<div class="form-group">
											<label for="productdesc">部门描述</label>
											<textarea v-model="form.description" class="form-control" id="productdesc"
												rows="5" readonly></textarea>
										</div>
									</form>

								</div>
							</tab-content>
							<el-button type="primary" plain slot="prev">返回</el-button>
							<el-button type="primary" plain slot="next">下一步</el-button>
							<el-button type="primary" plain slot="finish">提交申请</el-button>
						</form-wizard>
					</div>
				</div>
			</div>
		</div>
	</Layout>
</template>
